<template>
  <div class="header">
    <ul>
      <li v-for="item in urlList"
          :key="item.url">
        <router-link :to="item.url"
                     active-class="active"
                     exact>{{item.name}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  data () {
    return {
      urlList: [
        {
          url: '/home',
          name: '首页'
        },
        {
          url: '/download',
          name: '下载中心'
        },
        {
          url: '/news',
          name: '新闻资讯'
        },
        // {
        //   url: '/talk',
        //   name: '官方论坛'
        // },
        {
          url: '/plays',
          name: '玩法技巧'
        },
        {
          url: '/about',
          name: '关乎我们'
        },
        {
          url: '/answer',
          name: '疑难解答'
        },
        {
          url: '/conact',
          name: '联系我们'
        }
      ]
    }
  }
}
</script>
<style lang="scss" scoped>
.header {
  ul {
    height: 80px;
    background-color: #92d1e6;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    li {
      padding: 0 10px;
      position: relative;
      width: 80px;
      text-align: center;
      ::before {
        position: absolute;
        top: 4px;
        left: -2px;
        content: ' ';
        width: 2px;
        height: 17px;
        background-color: #ffffff;
      }
      &:last-child::after {
        position: absolute;
        top: 4px;
        right: 2px;
        content: ' ';
        width: 2px;
        height: 17px;
        background-color: #ffffff;
      }
      a {
        font-size: 18px;
        line-height: 26px;
        letter-spacing: 0px;
        color: #fff;
        &.active {
          color: #00ace6;
        }
      }
    }
  }
}
</style>



